.conditions-wrap {
  display: flex;
  gap: 10px;
  margin-bottom: 10px;
  padding-bottom: 20px;
  align-items: flex-end;
  border-bottom: 2px solid #e5e5e5;
}
.conditions-wrap label {
  display: flex;
  flex-direction: column;
}

.range-input {
  width: 100px;
}

.wrap {
  font-size: 16px;
}
.wrap h1 {
  margin-bottom: 64px;
}
.wrap__inner.loading {
  opacity: 0.5;
  pointer-events: none;
}

.btn {
  min-height: 30px;
  text-align: center;
  padding: 6px 10px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  text-transform: uppercase;
  font-size: 14px;
}
.btn[disabled] {
  cursor: not-allowed;
  opacity: 0.5;
}
.btn.btn-primary {
  background-color: #6e0000;
  color: #fff;
  border: 1px solid #9b1818;
  padding: 10px 20px;
}
.btn.btn-primary:hover {
  background-color: #fff;
  color: #9b1818;
}
.btn.btn-secondary {
  background-color: #ffffff;
  color: #000ba5;
  border: 1px solid #000875;
}
.btn.btn-secondary:hover {
  background-color: #000ba5;
  color: #ffffff;
}

.conditions li {
  max-width: 500px;
  margin-bottom: 24px;
  padding: 16px;
  border: 1px solid #e5e5e5;
  border-radius: 5px;
}
.conditions__item-inner {
  display: flex;
  align-items: center;
}
.conditions__item-inner button {
  margin-left: auto;
}
.conditions__item-inner .value {
  margin-right: 20px;
  font-weight: bold;
}
.conditions__item-inner .value.message {
  word-break: break-word;
}
.conditions__item-inner .label {
  margin-right: 4px;
  font-weight: normal;
  font-size: 14px;
  color: #666666;
  text-transform: uppercase;
}